import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AppsIcon from '@material-ui/icons/Apps';

export default function SimpleMenu() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <IconButton aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick} style={{color: "#c89666"}}>
        <AppsIcon fontSize="large"/>
      </IconButton>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {/* <MenuItem onClick={handleClose}>Favorite Recipes</MenuItem> */}
        {/* <MenuItem onClick={handleClose}>Favorite Photos</MenuItem> */}
        {/* <MenuItem onClick={handleClose}>Faith</MenuItem> */}
        <MenuItem onClick={handleClose}>In Progress</MenuItem>
      </Menu>
    </div>
  );
}